import React, { Component } from 'react';
import { connect } from 'react-redux';
import Scrollbars from '@isomorphic/shared/isomorphic/components/utility/customScrollBar';
import { InputSearch } from '@isomorphic/shared/isomorphic/components/uielements/input';
import mailList from '@isomorphic/shared/isomorphic/components/Mail/MailList';
import mailBuckets from '@isomorphic/shared/isomorphic/components/Mail/MailBuckets';
import mailTags from '@isomorphic/shared/isomorphic/components/Mail/MailTags';
import singleMail from '@isomorphic/shared/isomorphic/components/Mail/SingleMail';
import ComposeBtn from '@isomorphic/shared/isomorphic/components/Mail/MailComposeBtn';
import ComposeMail from '@isomorphic/shared/isomorphic/components/Mail/ComposeMail';
import PaginationControl from '@isomorphic/shared/isomorphic/components/Mail/MailPagination';
import IntlMessages from '@isomorphic/shared/isomorphic/components/utility/intlMessages';
import mailActions from '../../redux/mail/actions';
import mailSelector from '../../redux/mail/selector';
import MailBox from './mailBox.style';

const {
  filterAction,
  selectMail,
  changeComposeMail,
  changeReplyMail,
  changeSearchString,
  sendHTML
} = mailActions;

class DesktopView extends Component {
  constructor(props) {
    super(props);
    this.state = {
      search: this.props.searchString,
    };
  }

  callbackFunction = (childData) => {

    this.props.sendHTML(childData.html)
  }

  render() {
    const {
      allMails,
      filterAttr,
      filterMails,
      selectedMail,
      composeMail,
      replyMail,
      selectMail,
      filterAction,
      changeComposeMail,
      changeReplyMail,
      changeSearchString,
    } = this.props;
    const { search } = this.state;
    let singleMailComponent = (
      <p className="isoNoMailMsg">
        <IntlMessages id="email.noMessage" />
      </p>
    );
    const index = allMails.findIndex(mail => mail.id === selectedMail);
    if (index !== -1) {
      singleMailComponent = singleMail(
        allMails,
        filterMails,
        index,
        replyMail,
        changeReplyMail,
        selectMail,
        sendHTML
      );
    }
    return (
      <MailBox className="isomorphicMailBox">
        <div className="isoLeftWrapper">
          <ComposeBtn changeComposeMail={changeComposeMail} />
          <div className="isoMailOptions">
            <Scrollbars style={{ height: this.props.height - 70 }}>
              {mailBuckets(allMails, filterAction, filterAttr)}
              {mailTags(allMails, filterAction, filterAttr)}
            </Scrollbars>
          </div>
        </div>
        {composeMail ? (
          ''
        ) : (
          <div className="isoMiddleWrapper">
            <div className="isoBucketLabel">
              <h3>{filterAttr.bucket}</h3>
              <PaginationControl />
            </div>
            <div className="isoSearchMailWrapper">
              <InputSearch
                placeholder="Search Email"
                value={search}
                className="isoSearchEmail"
                onChange={event =>
                  this.setState({ search: event.target.value })
                }
                onSearch={value => changeSearchString(value)}
              />
            </div>
            <Scrollbars>
              {mailList(filterMails, selectMail, selectedMail)}
            </Scrollbars>
          </div>
        )}
        <div className="isoSingleMailWrapper">
          <Scrollbars style={{ height: this.props.height - 70 }}>
            {composeMail ? (
              <ComposeMail allMails={allMails} parentCallback = {this.callbackFunction}/>
            ) : (
              singleMailComponent
            )}
          </Scrollbars>
        </div>
      </MailBox>
    );
  }
}

function mapStateToProps(state) {
  const {
    allMails,
    tag,
    selectedMail,
    filterAttr,
    composeMail,
    replyMail,
    searchString,
  } = state.Mails;
  return {
    allMails,
    tag,
    selectedMail,
    filterAttr,
    composeMail,
    replyMail,
    searchString,
    filterMails: mailSelector(state.Mails),
  };
}
export default connect(
  mapStateToProps,
  {
    filterAction,
    selectMail,
    changeComposeMail,
    changeReplyMail,
    changeSearchString,
    sendHTML
  }
)(DesktopView);
